* {
  padding: 0;
  margin: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
html{
  scroll-behavior: smooth;
}

body {
  width: 100vw;
  overflow-x: hidden;
  scroll-behavior: smooth;
  background-color: white;
}



.stick {
  width: 100%;
  -webkit-box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.199);
          box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.199);
  position: absolute;
  top: 0;
  position: fixed;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 10px;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  z-index: 10;
  background-color: rgb(255, 255, 255);
}

.stick h1 {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  color: rgb(148, 18, 173);
  font-size: 30px;
  margin-left: 40%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}

.stick h2 {
  color: rgb(87, 0, 128);
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}

.stick .ham {
  width: 30px;
  height: 20px;
  margin-left: 35%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  margin-top: 10px;
  cursor: pointer;
}

.stick .ham div {
  width: 100%;
  height: 2.5px;
  background-color: purple;
  border-radius: 30px;
  margin: 2px;
}

.stick .ham div:nth-child(1) {
  width: 90%;
}

.stick .ham div:nth-child(2) {
  width: 80%;
}

.oe {
  width: 100%;
  position: absolute;
  top: 40px;
  position: fixed;
  z-index: 9;
  
 
}

.oe .row {
  width: 100%;
  list-style-type: none;
  padding: 10px;
  background-color: rgb(252, 252, 252);
  position: absolute;
  -webkit-box-shadow: 0px 0px 6px rgba(0, 0, 0, 0.199);
          box-shadow: 0px 0px 6px rgba(0, 0, 0, 0.199);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  position: fixed;
  z-index: 1;
}

.oe .row li {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-weight: lighter;
  font-size: 14px;
  color: black;
  margin-top: 10px;
  padding-right: 30px;
}

.oe .row li:nth-child(7) {
  border: none;
}
.slider{
  width: 100%;
  height: 100vh;
  background-color: rgb(255, 255, 255);
  position: relative;
  margin-top: 10px;
  position: fixed;
  transform: translateX(-110%);
  transition: transform .5s;
  z-index: 5;
  
 
}
.slid{
  width: 100%;
  height: 100vh;
  background-color: rgb(255, 255, 255);
  position: relative;
  margin-top: 10px;
  position: fixed;
  transition: transform 1s;
  z-index: 5;
 transform: translateX(0);

}
.options{
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  list-style-type: none;
  border-bottom: 1px solid rgb(134, 4, 146);
}
.options li{
  width: 20%;
  height: 100%;
 text-transform: capitalize;
 font-weight: bolder;
  padding: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
cursor: pointer;


}.options li:hover{
  background-color: rgb(134, 4, 146);
  color: white;
}
.lst{
  width: 90%;
  height: 70%;
  
  margin-top: 20px;
  margin-left: 50%;
  transform: translateX(-50%);
  list-style-type: none;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  
}
.lst li{
  width: 100%;
  height: 23%;
 
 text-align: center;
  display: flex;
  justify-content: center;
  flex-direction: column;
} 
.lst li h2{
text-transform: capitalize;
}
.lst li h6{
  color: rgba(0, 0, 0, 0.575);
  margin-top: 20px;
}
.categori{
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  margin: 10px;
  border: 1px solid rgba(0, 0, 0, 0.753);
  border-radius: 5px;
  padding: 5px;
  text-transform: uppercase;
}
.categori:hover{
  background-color: black;
  color: white;
}


 @media(max-width:450px){
  .categori{
    font-size: 14;
    width: 50%;
    text-align: center;
   margin-top: 50px;
  }
   .lst{
     height: 50%;
     width: 50%;
   }
  .lst li h6{
    
    margin-top: 10px;
    }
  .lst li h2{
    font-size: 18px;
    }
.options li{
  font-size: 14px;
}

  .oe .row {
    overflow: scroll;
    padding: 10px;
  }
  .oe .row::-webkit-scrollbar {
    display: none;
  }
  .oe .row li {
    padding-right: 10px;
    margin-right: 20px;
    text-align: center;
  }
  .stick h1 {
    font-size: 25px;
    margin-left: 45%;
    
  }
  .stick .ham {
    margin-left: 10%;
    width: 20px;
    height: 15px;
    margin-top: 5px;
    
  }
  .stick .ham div {
    height: 2px;
    margin: 2px;
  }
  .slider{
    
    height: 210vh;
    
    
   
  }
  

 }